import { Component, OnInit, ViewChild } from '@angular/core';
import { SpectDetail } from '../../_models/SpectDetail';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { LoadspecsService } from '../../_services/loadspecs.service';
import { AlertifyService } from '../../_services/alertify.service';

@Component({
  selector: 'app-manage-ram',
  templateUrl: './manage-ram.component.html',
  styleUrls: ['./manage-ram.component.css']
})
export class ManageRamComponent implements OnInit {
  model: any = {};
  rams: SpectDetail[];
  roleid: any =  Number(localStorage.getItem('RoleID'));
  @ViewChild('detailModal') public detailModal: ModalDirective;
  @ViewChild('EditModal') public EditModal: ModalDirective;

  constructor(private loadspecsService: LoadspecsService, private alertify: AlertifyService) { }

  ngOnInit(): void {
    this.loadRAM();
  }
  loadRAM() {
    return this.loadspecsService.loadSpectDetails().subscribe((rams: SpectDetail[]) => {
      this.rams = rams.filter(proj => (proj.parentId === 1));
    }, error => {
      this.alertify.error(error);
    });
  }

  AddRAM() {
    this.model.ParentId = 1;
    if (!this.model.Text) {
      this.alertify.error('Enter RAM');
      return;
    }
    this.loadspecsService.Addspecs(this.model).subscribe((rams: SpectDetail[]) => {
      this.rams = rams.filter(proj => (proj.parentId === 1));
      this.alertify.success('RAM Added Successful');
      this.detailModal.hide();
      this.model.Text = '';
    }, error => {
      this.alertify.error(error);
    });
  }

  EditRAM(id: number, Text: string) {
    this.model.Id = id;
    this.model.Text = Text;
    this.EditModal.show();
  }

  EditRAMPost() {
    if (!this.model.Text) {
      this.alertify.error('Enter RAM');
      return;
    }
    this.loadspecsService.Editspecs(this.model).subscribe((rams: SpectDetail[]) => {
      this.rams = rams.filter(proj => (proj.parentId === 1));
      this.alertify.success('RAM Edited Successful');
      this.EditModal.hide();
    }, error => {
      this.alertify.error(error);
    });

  }

}
